export default {
	data() {
		return {
			active: {
				card: 0,
				chain: 0
			},
			list: [1, 2, 3, 4]
		};
	},

	computed: {
		chainPreviousMargin() {
			switch (this.active.chain) {
				case 0:
					return '20rpx';
				case 3:
					return '120rpx';
				default:
					return '70rpx';
			}
		},
		chainNextMargin() {
			switch (this.active.chain) {
				case 0:
					return '120rpx';
				case 3:
					return '20rpx';
				default:
					return '70rpx';
			}
		}
	},

	methods: {
		onChangeCard(e) {
			this.active.card = e.detail.current;
		},
		onChangeChain(e) {
			this.active.chain = e.detail.current;
		}
	}
};
